.tweet{
    background-color: #dccdcd;
    padding: 5px;
    color:black;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-row-gap: 10px;
    margin-bottom: 5px;
    
}

.tweet img{
    border-radius: 100%;
    height:50%;
}

.likes:hover{
 cursor: pointer;
}

.new-tweet{
    width:100%;
    display: gird;
    grid-template-columns: repeat(1,1fr);
    padding:10px;
 }
.new-tweet-btn{
    background-color: lightblue;
    height:50px;
    color:black;
    padding:2px;
    font-size: 1.1em;
    align-self: center;
    width:100%;
}
.new-tweet>textarea{
    align-self: center;
    font-size: 1.1em;
    width:100%;
}


.red-heart{
    color:red;

}

/* for desktops*/
@media (min-width:801px) {

    .new-tweet{
        width:100vw;
        height:100vh;
       background-color: black;

    } 
    .new-tweet>textarea{
       padding:auto auto;
    }


}



